.product__rating {
  display: flex;
}

.product__starIcon {
  color: #f7bb44 !important;
}
.product {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  max-height: 400px;
  min-width: 100px;
  margin: 10px;
  padding: 20px;
  width: 100%;
  background-color: white;
  z-index: 1;
}

.product__info {
  height: 120px;
  margin-bottom: 15px;
}

.product__price {
  margin-top: 5px;
}
.product > img {
  width: 100%;
  max-height: 200px;
  object-fit: contain;
  margin-bottom: 15px;
}

.product > button {
  background-color: #f0c1b4;
  border: 1px solid;
  border-color: #a88734 #9c7e31 #846a29;
}
